.card__wrapper{
    width: 100%;
    padding: 2.5rem;
    display: flex;
    justify-content: space-between;
    
    .card{
        width: 258px;
        height: 134px;
        padding: 2rem;
        border-radius: 10px;
        gap: 2rem;
        box-shadow: 0 0 8px gray;

        span{
            color: #3751FF;

            h2{
                color: #3751FF;

            }

        }

        h1{
            text-align: center;
            font-size: 40px;
            line-height: 50px;
            letter-spacing: 1px;
            border-radius: 10px;
        }
        h2{
            text-align: center;
            color: #9FA2B4;
            font-size: 19px;
            line-height: 22px;
            letter-spacing: 0.4px;
        }
    }

}
