.tickets{
    width: 100%;
    padding: 1rem;

    input{
        width: 80%;
        gap: 1rem;
        height: 30px;
        background-color: lightseagreen;
        border: none;
        color: white;
        font-size: 30px;
    }
    button{
        width: 150px;
        height: 30px;
        background-color: black;
        color: white;
        cursor: pointer;
        margin-top: -400px;
    }
}
.students{
    width: 100%;
    box-shadow: 0 0 16px gray;
    gap: 2rem;
    margin: auto;

    span{
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2px;
        color: #252733;

        li{
            text-decoration: none;
            list-style: none;
        }
    }

    img{
        border-radius: 10px;
    }
}