.form{
    width: 100%;
    padding: 1rem;
    background-color: black;

    form{
        display: flex;
        gap: 2rem;

        input{
            width: 200px;
            height: 60px;
            margin-top: 50px;
        }
        textarea{
            width: 200px;
            height: 60px;
            margin-top: 50px;
            text-align: center;
        }
        select{
            width: 200px;
            height: 60px;
            margin-top: 50px;
        }
    }
    button{
        width: 200px;
        height: 60px;
        margin-top: 50px;
    }
}